import { ChangeEvent } from 'react';
import { FieldValues, UseFormRegister } from 'react-hook-form';

import * as Styles from './TextInput.styles';

type TextInputProps = {
  name?: string;
  disabled?: boolean;
  value?: string;
  type?: string;
  placeholder?: string;
  register?: UseFormRegister<FieldValues>; // For react-hook-form controlled components
  onChange?: (value: string) => void; // For other use cases
};

export const TextInput = ({
  disabled,
  name,
  onChange,
  placeholder,
  register,
  type,
  value,
}: TextInputProps) => (
  <Styles.Input
    disabled={disabled}
    value={value}
    placeholder={placeholder}
    type={type || 'text'}
    {...(name && register ? { ...register(name) } : {})}
    onChange={(event: ChangeEvent<HTMLInputElement>) =>
      onChange?.(event.target.value)
    }
  />
);
