import {
  cloneElement,
  FormEvent,
  Fragment,
  ReactElement,
  useState,
} from 'react';
import { FieldValues, UseFormRegister } from 'react-hook-form';

import { ToggleItemProps } from './';
import * as Styles from './Toggle.styles';

export interface ToggleProps {
  children: ReactElement<ToggleItemProps>[];
  name: string;
  value?: string;
  onChange?: (event: FormEvent, value: string) => void;
  register?: UseFormRegister<FieldValues>;
}

export const Toggle = ({
  children,
  name,
  onChange,
  register,
  value,
  ...controlProps
}: ToggleProps) => {
  const [selectedValue, setSelectedValue] = useState(value);

  const handleChange = (event: FormEvent<HTMLInputElement>) => {
    setSelectedValue(event.currentTarget.value);
    onChange?.(event, event.currentTarget.value);
  };

  return (
    <Styles.Wrapper {...controlProps} role="radiogroup">
      {children.map((child, index) => (
        <Fragment key={index}>
          {cloneElement(child, {
            key: index,
            onChange: handleChange,
            name,
            register,
            checked: child.props.value === selectedValue,
          })}
          {index !== children.length - 1 && (
            <Styles.Separator isShown={!selectedValue} />
          )}
        </Fragment>
      ))}
    </Styles.Wrapper>
  );
};
