import { Button } from 'components/Button';
import { useTimeZoneContext } from 'contexts/TimeZone';
import { useOrder } from 'hooks/useOrder';
import { Order, OrderType } from 'types';
import {
  formatDate,
  formatDateTimeWithTimezone,
  parseApiDate,
} from 'utils/date';
import { formatPrice, formatQuantity, formatValue } from 'utils/format';
import { formatOrderType } from 'utils/order';

import { Header } from './Header';
import * as Styles from './OrderPanel.styles';

type Props = {
  orderId: Order['id'];
};

export const OrderDetails = (props: Props) => {
  const { orderId } = props;
  const { data: order, isError, isLoading } = useOrder(orderId);
  const { timeZone } = useTimeZoneContext();

  if (isLoading || isError || !order) {
    // TODO: replace with proper loading and error states
    return null;
  }

  const isLimitOrder = order.displayStringOrderType === OrderType.limit;
  const isReferenceOrder = order.displayStringOrderType === OrderType.reference;

  return (
    <>
      <Header>
        <Styles.HeaderTitle>{order.displayStringOrderType}</Styles.HeaderTitle>
        <Styles.Actions>
          <Button variant="outlined" color="secondary" size="small">
            History
          </Button>
        </Styles.Actions>
        <Styles.Actions>
          <Button variant="outlined" color="secondary" size="small">
            Copy
          </Button>
          <Button variant="outlined" color="secondary" size="small">
            Edit
          </Button>
        </Styles.Actions>
      </Header>
      <Styles.Content side={order.displayStringBuySell}>
        <Styles.SubHeader>
          <Styles.SubHeaderText>Order ID: {order.id}</Styles.SubHeaderText>
          <Styles.SubHeaderText>
            Exec:{' '}
            {formatDateTimeWithTimezone(
              parseApiDate(order.createdAt),
              timeZone
            )}
          </Styles.SubHeaderText>
          <Styles.Status>{order.displayStringOrderStatus}</Styles.Status>
        </Styles.SubHeader>
        <Styles.Details>
          <Styles.Summary>
            <Styles.SectionRow>
              <Styles.ThirdColumn>
                <Styles.SectionRow>
                  <Styles.KeyValue>
                    <Styles.Key>Trade side</Styles.Key>
                    <Styles.TradeSide side={order.displayStringBuySell}>
                      {order.displayStringBuySell}
                    </Styles.TradeSide>
                  </Styles.KeyValue>
                  <Styles.KeyValue>
                    <Styles.Key>Order type</Styles.Key>
                    <Styles.ValueTag>
                      {formatOrderType(order.displayStringOrderType)}
                    </Styles.ValueTag>
                  </Styles.KeyValue>
                </Styles.SectionRow>
              </Styles.ThirdColumn>

              <Styles.KeyValue>
                <Styles.SecondaryKey>Order reference</Styles.SecondaryKey>
                <Styles.Value>{formatValue(order.reference)}</Styles.Value>
              </Styles.KeyValue>
            </Styles.SectionRow>

            <Styles.SectionRow>
              <Styles.ThirdColumn>
                <Styles.KeyValue>
                  <Styles.Key>JM PMM unit</Styles.Key>
                  <Styles.Value>
                    {formatValue(order.displayStringInternalBu)}
                  </Styles.Value>
                </Styles.KeyValue>
              </Styles.ThirdColumn>

              <Styles.KeyValue>
                <Styles.Key>Counterparty</Styles.Key>
                <Styles.Value>
                  {formatValue(order.displayStringExternalBu)}
                </Styles.Value>
              </Styles.KeyValue>
            </Styles.SectionRow>
          </Styles.Summary>

          <Styles.PrimarySection>
            {/* TODO: Add info tooltip */}
            <Styles.SectionTitle>Metal Information</Styles.SectionTitle>
            <Styles.SectionRow>
              <Styles.ThirdColumn>
                <Styles.KeyValue>
                  <Styles.Key>Product ticker</Styles.Key>
                  <Styles.Value>
                    {formatValue(order.displayStringTicker)}
                  </Styles.Value>
                </Styles.KeyValue>
              </Styles.ThirdColumn>

              <Styles.ThirdColumn>
                <Styles.KeyValue>
                  <Styles.Key>Unit</Styles.Key>
                  <Styles.Value>
                    {formatValue(order.displayStringBaseQuantityUnit)}
                  </Styles.Value>
                </Styles.KeyValue>
              </Styles.ThirdColumn>

              <Styles.ThirdColumn>
                <Styles.KeyValue>
                  <Styles.Key>Metal QTY</Styles.Key>
                  <Styles.Value>
                    {formatQuantity(order.baseQuantity)}
                  </Styles.Value>
                </Styles.KeyValue>
              </Styles.ThirdColumn>
            </Styles.SectionRow>
          </Styles.PrimarySection>

          {isLimitOrder && (
            <>
              <Styles.PrimarySection>
                {/* TODO: Add info tooltip */}
                <Styles.SectionTitle>Contract Terms</Styles.SectionTitle>
                <Styles.SectionRow>
                  <Styles.ThirdColumn>
                    <Styles.KeyValue>
                      <Styles.Value>
                        {formatValue(order.displayStringPriceType)}
                      </Styles.Value>
                    </Styles.KeyValue>
                  </Styles.ThirdColumn>

                  <Styles.ThirdColumn>
                    <Styles.KeyValue>
                      <Styles.Value>
                        {formatValue(order.displayStringContractType)}
                      </Styles.Value>
                    </Styles.KeyValue>
                  </Styles.ThirdColumn>
                </Styles.SectionRow>
              </Styles.PrimarySection>

              <Styles.SecondarySection>
                <Styles.SectionRow>
                  <Styles.ThirdColumn>
                    <Styles.KeyValue>
                      <Styles.Key>Limit price</Styles.Key>
                      <Styles.Value>
                        {Number(formatPrice(order.limitPrice))}
                      </Styles.Value>
                    </Styles.KeyValue>
                  </Styles.ThirdColumn>

                  <Styles.ThirdColumn>
                    <Styles.KeyValue>
                      <Styles.Key>Fixed start date</Styles.Key>
                      <Styles.Value>
                        {formatDate(parseApiDate(order.startDateConcrete))}
                      </Styles.Value>
                    </Styles.KeyValue>
                  </Styles.ThirdColumn>

                  <Styles.ThirdColumn>
                    <Styles.KeyValue>
                      <Styles.Key>Settle date</Styles.Key>
                      <Styles.Value>
                        {formatDate(parseApiDate(order.settleDate))}
                      </Styles.Value>
                    </Styles.KeyValue>
                  </Styles.ThirdColumn>
                </Styles.SectionRow>
              </Styles.SecondarySection>

              <Styles.SecondarySection>
                <Styles.SectionRow>
                  <Styles.ThirdColumn>
                    <Styles.KeyValue>
                      <Styles.Key>Validation type</Styles.Key>
                      <Styles.Value>
                        {formatValue(order.displayStringValidationType)}
                      </Styles.Value>
                    </Styles.KeyValue>
                  </Styles.ThirdColumn>

                  <Styles.ThirdColumn>
                    <Styles.KeyValue>
                      <Styles.Key>Part fillable</Styles.Key>
                      <Styles.Value>
                        {formatValue(order.displayStringPartFillable)}
                      </Styles.Value>
                    </Styles.KeyValue>
                  </Styles.ThirdColumn>
                </Styles.SectionRow>
              </Styles.SecondarySection>
            </>
          )}

          <Styles.SecondarySection>
            <Styles.SectionRow>
              <Styles.ThirdColumn>
                <Styles.KeyValue>
                  <Styles.Key>Location</Styles.Key>
                  <Styles.Value>
                    {formatValue(order.displayStringMetalLocation)}
                  </Styles.Value>
                </Styles.KeyValue>
              </Styles.ThirdColumn>

              {isReferenceOrder && <Styles.ThirdColumn />}

              <Styles.ThirdColumn>
                <Styles.KeyValue>
                  <Styles.Key>Metal form</Styles.Key>
                  <Styles.Value>
                    {formatValue(order.displayStringMetalForm)}
                  </Styles.Value>
                </Styles.KeyValue>
              </Styles.ThirdColumn>
            </Styles.SectionRow>
          </Styles.SecondarySection>

          {isReferenceOrder && (
            <Styles.SecondarySection>
              <Styles.SecondarySectionTitle>
                Spread
              </Styles.SecondarySectionTitle>
              <Styles.SectionRow>
                <Styles.ThirdColumn>
                  <Styles.KeyValue>
                    <Styles.Key>Metal Price</Styles.Key>
                    <Styles.Value>
                      {formatPrice(order.metalPriceSpread)}
                    </Styles.Value>
                  </Styles.KeyValue>
                </Styles.ThirdColumn>

                <Styles.ThirdColumn>
                  <Styles.KeyValue>
                    <Styles.Key>FX Rate</Styles.Key>
                    <Styles.Value>
                      {formatPrice(order.fxRateSpread)}
                    </Styles.Value>
                  </Styles.KeyValue>
                </Styles.ThirdColumn>

                <Styles.ThirdColumn>
                  <Styles.KeyValue>
                    <Styles.Key>Cont/backward %</Styles.Key>
                    <Styles.Value>
                      {formatPrice(order.contangoBackwardation)}
                    </Styles.Value>
                  </Styles.KeyValue>
                </Styles.ThirdColumn>
              </Styles.SectionRow>
            </Styles.SecondarySection>
          )}

          <Styles.SecondarySection>
            <Styles.KeyValue>
              <Styles.SecondaryKey>Comments</Styles.SecondaryKey>
              {/* TODO: Integrate with comments endpoint to get real list of comments */}
              <Styles.Value>-</Styles.Value>
            </Styles.KeyValue>
          </Styles.SecondarySection>
        </Styles.Details>
      </Styles.Content>
      <Styles.Footer>
        <Styles.Actions>
          <Button color="error">Reject Order</Button>
          <Button variant="outlined" color="error">
            Pull Order
          </Button>
        </Styles.Actions>
        <Styles.Actions>
          <Button>Confirm Order</Button>
        </Styles.Actions>
      </Styles.Footer>
    </>
  );
};
