import isEmpty from 'lodash/isEmpty';
import { useState } from 'react';
import { CustomElement, FieldValues, useForm } from 'react-hook-form';

import { Button } from 'components/Button';
import { DateInput } from 'components/DateInput';
import { FormControlLabel } from 'components/FormControlLabel';
import { ChevronThinRightIcon } from 'components/Icon';
import { Select } from 'components/Select';
import { TextArea } from 'components/TextArea';
import { TextInput } from 'components/TextInput';
import { Toggle, ToggleItem } from 'components/Toggle';
import { Tooltip } from 'components/Tooltip';
import { useRefData } from 'hooks/useRefData';
import {
  ContractTerms,
  OrderSide,
  OrderType,
  RefTypeIds,
  ValidationType,
} from 'types';
import { Nullable } from 'types/util';
import { getSelectOptionsFromRefData } from 'utils/form';
import { formatOrderType } from 'utils/order';
import { getRefDataByTypeId } from 'utils/references';

import { Header } from './Header';
import { getProductTickerOptions } from './helpers';
import * as Styles from './OrderPanel.styles';

export const NewOrder = () => {
  const [orderType, setOrderType] = useState<Nullable<OrderType>>(null);
  const [orderSide, setOrderSide] = useState<OrderSide | undefined>(undefined);
  const refData = useRefData();
  const {
    formState: { errors },
    handleSubmit,
    register,
  } = useForm();

  const productTickers = getRefDataByTypeId(refData, RefTypeIds.TICKER);
  const metalUnits = getRefDataByTypeId(refData, RefTypeIds.METAL_UNIT);
  const metalForms = getRefDataByTypeId(refData, RefTypeIds.METAL_FORM);
  const metalLocations = getRefDataByTypeId(refData, RefTypeIds.METAL_LOCATION);

  const onSubmit = (data: any) => console.log('submitted', data);

  // TODO - fix this to include register type
  const getFormFieldProps = (props = {}): CustomElement<FieldValues> & any => ({
    register,
    ...props,
  });

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Header>
        <Styles.HeaderTitle>{orderType || 'New Order'}</Styles.HeaderTitle>
        <Styles.Actions>
          <Button variant="outlined" color="secondary" size="small">
            History
          </Button>
        </Styles.Actions>
        <Styles.Actions>
          <Button variant="outlined" color="secondary" size="small">
            Copy
          </Button>
          <Button variant="outlined" color="secondary" size="small">
            Edit
          </Button>
        </Styles.Actions>
      </Header>
      <Styles.Content side={orderSide}>
        <Styles.Details>
          <Styles.Summary>
            <Styles.SectionRow>
              <Styles.ToggleWrapper>
                <FormControlLabel
                  label="Trade side"
                  labelPlacement="start"
                  control={
                    <Toggle
                      onChange={(_, value) => setOrderSide(value as OrderSide)}
                      {...getFormFieldProps({ name: 'tradeSide' })}
                    >
                      <ToggleItem side={OrderSide.buy} value={OrderSide.buy} />
                      <ToggleItem
                        side={OrderSide.sell}
                        value={OrderSide.sell}
                      />
                    </Toggle>
                  }
                />
                <FormControlLabel
                  label="Order type"
                  labelPlacement="start"
                  control={
                    <Toggle
                      onChange={(_, value) => setOrderType(value as OrderType)}
                      {...getFormFieldProps({ name: 'orderType' })}
                    >
                      <ToggleItem value={OrderType.limit}>
                        {formatOrderType(OrderType.limit)}
                      </ToggleItem>
                      <ToggleItem value={OrderType.reference}>
                        {formatOrderType(OrderType.reference)}
                      </ToggleItem>
                    </Toggle>
                  }
                />
              </Styles.ToggleWrapper>

              <FormControlLabel
                label="Order reference (Optional)"
                isOptional
                labelPlacement="start"
                control={
                  <TextInput {...getFormFieldProps({ name: 'orderRef' })} />
                }
              />
            </Styles.SectionRow>

            <Styles.SectionRow>
              <Styles.ThirdColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="JM PMM unit"
                  control={
                    <Select {...getFormFieldProps({ name: 'jmPmmUnit' })} />
                  }
                />
              </Styles.ThirdColumn>

              <Styles.Column>
                <FormControlLabel
                  labelPlacement="start"
                  label="Counterparty"
                  control={
                    <TextInput
                      {...getFormFieldProps({ name: 'counterparty' })}
                    />
                  }
                />
              </Styles.Column>
            </Styles.SectionRow>
          </Styles.Summary>

          <Styles.PrimarySection>
            <Styles.SectionTitle>
              <Tooltip text="Tooltip text">Metal Information</Tooltip>
            </Styles.SectionTitle>
            <Styles.SectionRow>
              <Styles.ThirdColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Product ticker"
                  control={
                    <Select
                      options={getProductTickerOptions(productTickers)}
                      {...getFormFieldProps({ name: 'productTicker' })}
                    />
                  }
                />
              </Styles.ThirdColumn>

              <Styles.ThirdColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Unit"
                  control={
                    <Select
                      options={getSelectOptionsFromRefData(metalUnits)}
                      {...getFormFieldProps({ name: 'metalUnit' })}
                    />
                  }
                />
              </Styles.ThirdColumn>

              <Styles.ThirdColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Metal QTY"
                  control={
                    <TextInput
                      {...getFormFieldProps({
                        type: 'number',
                        name: 'metalQty',
                      })}
                    />
                  }
                />
              </Styles.ThirdColumn>
            </Styles.SectionRow>
          </Styles.PrimarySection>

          <Styles.PrimarySection>
            <Styles.SectionTitle>
              <Tooltip text="Tooltip text">Contract Terms</Tooltip>
            </Styles.SectionTitle>
            <Styles.SectionRow>
              <Styles.ThirdColumn>
                <Styles.SpotForwardWrapper>
                  <Toggle {...getFormFieldProps({ name: 'contractTerms' })}>
                    <ToggleItem value={ContractTerms.spot} />
                    <ToggleItem value={ContractTerms.forward} />
                  </Toggle>
                  <ChevronThinRightIcon />
                </Styles.SpotForwardWrapper>
              </Styles.ThirdColumn>

              <Styles.ThirdColumn>
                <Toggle
                  {...getFormFieldProps({
                    // TODO - rename to something clearer
                    name: 'contractTerms2',
                  })}
                >
                  <ToggleItem value={ContractTerms.fixed} />
                  <ToggleItem value={ContractTerms.relative} />
                </Toggle>
              </Styles.ThirdColumn>
            </Styles.SectionRow>
          </Styles.PrimarySection>

          <Styles.SecondarySection>
            <Styles.SectionRow>
              <Styles.ThirdColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Limit Price"
                  control={
                    <TextInput {...getFormFieldProps({ name: 'limitPrice' })} />
                  }
                />
              </Styles.ThirdColumn>

              <Styles.ThirdColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Start Date"
                  control={
                    <DateInput
                      onSelect={() => null}
                      {...getFormFieldProps({ name: 'startDate' })}
                    />
                  }
                />
              </Styles.ThirdColumn>

              <Styles.ThirdColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Settle Date"
                  control={
                    <DateInput
                      onSelect={() => null}
                      {...getFormFieldProps({ name: 'settleDate' })}
                    />
                  }
                />
              </Styles.ThirdColumn>
            </Styles.SectionRow>
          </Styles.SecondarySection>

          <Styles.SecondarySection>
            <Styles.SectionRow>
              <FormControlLabel
                labelPlacement="start"
                label="Validation Type"
                control={
                  <Toggle {...getFormFieldProps({ name: 'validationType' })}>
                    <ToggleItem value={ValidationType.goodTillCancelled} />
                    <ToggleItem value={ValidationType.expiryDate} />
                  </Toggle>
                }
              />

              <Styles.ThirdColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Part Fillable"
                  control={
                    <Toggle {...getFormFieldProps({ name: 'partFillable' })}>
                      <ToggleItem value="Yes" />
                      <ToggleItem value="No" />
                    </Toggle>
                  }
                />
              </Styles.ThirdColumn>
            </Styles.SectionRow>
          </Styles.SecondarySection>

          <Styles.SecondarySection>
            <Styles.SectionRow>
              <Styles.HalfColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Location"
                  control={
                    <Select
                      options={getSelectOptionsFromRefData(metalLocations)}
                      {...getFormFieldProps({ name: 'location' })}
                    />
                  }
                />
              </Styles.HalfColumn>

              <Styles.HalfColumn>
                <FormControlLabel
                  labelPlacement="start"
                  label="Metal Form"
                  control={
                    <Select
                      options={getSelectOptionsFromRefData(metalForms)}
                      {...getFormFieldProps({ name: 'metalForm' })}
                    />
                  }
                />
              </Styles.HalfColumn>
            </Styles.SectionRow>
          </Styles.SecondarySection>

          <Styles.SecondarySection>
            <FormControlLabel
              labelPlacement="start"
              label="Comments (Optional)"
              isOptional
              control={
                <TextArea
                  name="comments"
                  placeholder="Add your comment"
                  {...getFormFieldProps()}
                />
              }
            />
          </Styles.SecondarySection>
        </Styles.Details>
      </Styles.Content>
      <Styles.Footer>
        <Styles.Actions>
          <Button variant="outlined" color="secondary">
            Reset
          </Button>
        </Styles.Actions>
        <Styles.Actions>
          <Button disabled={!isEmpty(errors)} type="submit">
            Sumbit Order
          </Button>
        </Styles.Actions>
      </Styles.Footer>
    </form>
  );
};
