import { ChangeEvent } from 'react';
import { FieldValues, UseFormRegister } from 'react-hook-form';

import * as Styles from './TextArea.styles';

type TextAreaProps = {
  name?: string;
  disabled?: boolean;
  value?: string;
  placeholder?: string;
  register?: UseFormRegister<FieldValues>; // For react-hook-form controlled components
  onChange?: (value: string) => void; // For other use cases
};

export const TextArea = ({
  disabled,
  name,
  onChange,
  placeholder,
  register,
  value,
}: TextAreaProps) => (
  <Styles.Input
    disabled={disabled}
    value={value}
    placeholder={placeholder}
    {...(name && register ? { ...register(name) } : {})}
    onChange={(event: ChangeEvent<HTMLTextAreaElement>) =>
      onChange?.(event.target.value)
    }
  />
);
