import { useQuery } from 'react-query';

import { Order } from 'types/orders';
import { request } from 'utils';

import { OrdersResponse, QueryKeys } from './types';

export const useOrder = (id: Order['id']) => {
  return useQuery([QueryKeys.ORDER, id], async () => {
    const params = new URLSearchParams([['orderIds', String(id)]]);
    const data = await request<OrdersResponse>(`order?${params.toString()}`);

    return data.content[0];
  });
};
