import { REACT_APP_API_URL, REACT_APP_MOCK_API } from 'config';

type RequestMethodType = 'POST' | 'GET';
type RequestOptionsType = {
  body?:
    | Blob
    | BufferSource
    | FormData
    | URLSearchParams
    | ReadableStream<Uint8Array>
    | string
    | null;
  headers?: Record<string, string>;
  redirect?: RequestRedirect;
  credentials?: RequestCredentials;
  method?: RequestMethodType;
};

const HOST = REACT_APP_MOCK_API ? '' : REACT_APP_API_URL;

export const request = async <T>(url: string, options?: RequestOptionsType) => {
  try {
    const res = await fetch(`${HOST}/toms/${url}`, {
      ...options,
      headers: { ...options?.headers, Accept: 'application/json' },
    });

    const data = (await res.json()) as T;

    return data;
  } catch (error) {
    throw new Error(
      error instanceof Error ? error.message : 'Unknown server error'
    );
  }
};
