import { useRef, useState } from 'react';
import { useClickAway } from 'react-use';

import { DatePicker } from 'components/DatePicker';
import { CalendarIcon } from 'components/Icon';
import { NO_VALUE } from 'constants/format';

import * as Styles from './DateInput.styles';

type DateInputProps = {
  name?: string;
  onChange?: (value: string) => void;
  onSelect?: (dates: Date[]) => void;
  range?: boolean;
  value?: string;
};

export const DateInput = ({
  name,
  onChange,
  onSelect,
  range,
  value,
}: DateInputProps) => {
  const datePickerRef = useRef<HTMLDivElement>(null);
  const [isHidden, setIsHidden] = useState(true);

  useClickAway(datePickerRef, () => {
    if (!isHidden) {
      setIsHidden(true);
    }
  });

  return (
    <Styles.DateInput>
      <Styles.TextInput
        name={name}
        placeholder={NO_VALUE}
        onChange={(event) => onChange?.(event.target.value)}
        value={value}
      />
      <Styles.Button onClick={() => setIsHidden((isHidden) => !isHidden)}>
        <CalendarIcon />
      </Styles.Button>
      <Styles.DatePicker $isHidden={isHidden}>
        <DatePicker
          ref={datePickerRef}
          onSelected={(dates: Date[]) => onSelect?.(dates)}
          range={range}
        />
      </Styles.DatePicker>
    </Styles.DateInput>
  );
};
